import React, {useEffect, useState} from "react";
import './Feed.css'
import StoryReel from "./StoryReel";
import MessageSender from "./MessageSender";
import Post from "./Post";
import db from "../../firebase";

function Feed() {
    const [posts, setPosts] = useState([]);
    useEffect(() => {
        db.collection('posts')
            .orderBy('timestamp', 'desc')
            .onSnapshot(snapshot => (
            setPosts(snapshot.docs.map(doc => ({id: doc.id, data: doc.data()})))
        ))

    },[]);
    return (
        <div className='feed'>
            <StoryReel/>
            <MessageSender/>

            {posts.map(post => (
                <Post
                    key={post.data.id}
                    profilePic={post.data.profilePic}
                    message={post.data.message}
                    timestamp={post.data.timestamp}
                    username={post.data.username}
                    image={post.data.image}/>
            ))}
            {/*<Post*/}
            {/*    profilePic='https://mpak-suse1.akamaized.net/res/user_v_icon/371/icon_6540200603635717135-aYRWDagwOy.jpg'*/}
            {/*    message='This works'*/}
            {/*    timestamp='Timestamp'*/}
            {/*    username='sanztah'*/}
            {/*    image='https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRBMs-Dzj7gT8GAzk_6TDZwGTWYCSbwyTU8Dw&usqp=CAU'/>*/}
            {/*<Post*/}
            {/*    profilePic='data:image/jpeg;base64,'*/}
            {/*    message='This works 2'*/}
            {/*    timestamp='Timestamp'*/}
            {/*    username='sanztah'/>*/}
        </div>
    )
}

export default Feed
